<template>
  <div class="whole-menu-container">
        <div class="menu">
            <div class="upper flex relative">
                <ul>
                    <li style="transition:.4s .3s all" class="ayuda element">Ayuda</li>
                    <li style="transition:.4s .4s all" class="faq element">FAQ</li>
                </ul>
                <div class="hamburguer" @click="toggleNav">
                    <div class="linea1"></div>
                    <div class="linea2"></div>
                </div>
            </div>
            <div class="down">
                <ul>
                    <li style="transition:.4s .5s  all" class="ig element"><a href="https://www.instagram.com/holahaus/">Instagram</a></li>
                    <li style="transition:.4s .6s  all" class="sp element"><a href="https://www.facebook.com/holahaus/">Spotify</a></li>
                    <li style="transition:.4s .7s all" class="fb element"><a href="https://www.facebook.com/holahaus/">Facebook</a></li>
                </ul>
            </div>
        </div>
      </div>
</template>

<script>
export default {
    name:'Menu',
    methods:{
        toggleNav(){
            let line1 = document.querySelector('.line1')
            let linea1 = document.querySelector('.linea1')
            let linea2 = document.querySelector('.linea2')
            let line2 = document.querySelector('.line2')
            let menu = document.querySelector('.whole-menu-container')

            line1.style.transform = 'rotate(0)'
            line1.style.margin = '.5rem'

            linea1.style.transform = 'rotate(0)'
            linea1.style.margin = '.25rem'

            linea2.style.transform = 'rotate(0)'
            linea2.style.margin = '.25rem'

            line2.style.transform = 'rotate(0)'
            line2.style.margin = '.5rem'
            
            menu.style.opacity = '0'
            menu.style.pointerEvents = 'none'

            let element1 = document.querySelector('.ayuda')
            let element2 = document.querySelector('.faq')
            let element3 = document.querySelector('.ig')
            let element4 = document.querySelector('.sp')
            let element5 = document.querySelector('.fb')
            let elements = [element1,element2,element3,element4,element5]
            elements.forEach(function(element){
                element.style.opacity = '0'
                element.style.transform = 'translateX(-1rem)'
            })
        }
    }
}
</script>

<style scoped>
.whole-menu-container{
    background:var(--dark-color);
    position:fixed;
    opacity: 0;
    z-index: 1000000;
    pointer-events: none;
    top:0;
    height:100vh;
    width:100vw;
    transition:.4s all
}
.menu{
    position:relative;
    left:50%;
    transform:translateX(-50%);
    max-width: 75rem;
    max-height: 35rem;
    font-size: 2.5rem;
    display:flex;
    justify-content: space-between;
    flex-direction: column;
    height:100%;
    padding:2rem 0;
}
.menu .hamburguer{
    cursor:pointer;
    transition:.5s all;
    position:absolute;
    top:.5rem;
    right:1rem;
    padding:0 1rem;
}
.menu .hamburguer div{
    width:1.2rem;
    height:1px ;
    background:var(--clear-font);
    transition:.5s all
}
.menu .upper{
    font-weight: 300;
    color:var(--clear-font);
    justify-content: space-between;
    padding-top: 4rem
}
.upper ul li{
    margin-bottom: 1.5rem;
    cursor:pointer
}
.menu .down a{
    color:var(--rosa);
}
.down ul li{
    margin-top: 1.5rem;
    cursor:pointer
}
.whole-menu-container li{
    opacity:0;
    transform: translateX(-1rem);
}
</style>