import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/taller',
    name: 'taller',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/Taller.vue')
    }
  },
  {
    path: '/galeria',
    name: 'galeria',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/Galeria.vue')
    }
  },
  {
    path: '/comunidad',
    name: 'comunidad',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/Comunidad.vue')
    }
  },
  {
    path: '/tienda',
    name: 'tienda',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/Tienda.vue')
    }
  },
  {
    path: '/taller/Illustration-Toolbox',
    name: 'IlTool',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/subViews/IlTool.vue')
    }
  },
   {
     path: '/Illustration-Toolbox/completar-compra',
     name: 'IlluCompra',
     component: function () {
       return import(/* webpackChunkName: "about" */ '../views/subViews/IluCompra.vue')
     }
   },
]

const router = new VueRouter({
  routes,
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 };
  }
})


export default router
