<template>
    <div class="whole-nav-container">
        <div class="header">
            <router-link to="/">
                <div class="logo">
                    <img src="../assets/icons/logo.svg" alt="">
                </div>  
            </router-link>
            <div class="nav">
                <router-link to="/">inicio</router-link> 
                <router-link to="/taller">taller</router-link>
                <router-link to="/galeria">galería</router-link>
                <router-link to="/comunidad">comunidad</router-link>
                <router-link to="/tienda">tienda</router-link>
                <router-view/>
                <div class="burguer" @click="toggleNav">
                    <div class="line1"></div>
                    <div class="line2"></div>
                </div>
            </div>
      </div>
    </div>
</template>

<script>
export default {
    name:'navBar',
    methods:{
        toggleNav(){
            let line1 = document.querySelector('.line1')
            let linea1 = document.querySelector('.linea1')
            let linea2 = document.querySelector('.linea2')
            let line2 = document.querySelector('.line2')
            let menu = document.querySelector('.whole-menu-container')

            line1.style.transform = 'rotate(45deg)'
            line1.style.margin = '0'

            linea1.style.transform = 'rotate(45deg)'
            linea1.style.margin = '0'

            linea2.style.transform = 'rotate(-45deg)'
            linea2.style.margin = '0'

            line2.style.transform = 'rotate(-45deg)'
            line2.style.margin = '0'
            
            menu.style.opacity = '1'
            menu.style.pointerEvents = 'all'

            let element1 = document.querySelector('.ayuda')
            let element2 = document.querySelector('.faq')
            let element3 = document.querySelector('.ig')
            let element4 = document.querySelector('.sp')
            let element5 = document.querySelector('.fb')
            let elements = [element1,element2,element3,element4,element5]
            elements.forEach(function(element){
                element.style.opacity = '1'
                element.style.transform = 'translateX(0rem)'
            })

        }
    }
}
</script>

<style scoped>
.whole-nav-container{
    position:relative;
    left:50%;
    transform: translateX(-50%);
    background:#FCF4F5;
}
.header{
    display:flex;
    justify-content:space-between;
    align-items: center;
    width:100%;
    padding:2rem 0;
    position:relative;
    left:50%;
    transform:translateX(-50%);
    max-width: 75rem;
}

.nav{
    display:flex;
    align-items: center;
}
.nav a{
    margin-right: 2.5rem;
    transform:translateX(1.2rem)
}
.nav .burguer{
    cursor:pointer;
    transition:.5s all;
    padding:0 1rem;
}
.nav .burguer div{
    width:1.2rem;
    height:1px ;
    background:var(--dark-color);
    margin:.5rem;
    transition:.5s all
}
.nav a.router-link-exact-active {
  border-bottom: 2px solid var(--dark-color) ;
}
</style>